import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.css";
import "../../assets/stylesheets/main_styles.css";
import "./style.css";
import * as Stomp from "stompjs";
import SockJS from "sockjs-client";
import Player from '../player/player';
import Level from '../level/level';
import Menu from '../menu/menu';
import swal from 'sweetalert';
import Sidebar from '../sidebar/sidebar';

//DOOOOOOOOOOO NOOOOT TOUCH THIS COMMENT !
var socket = new SockJS('https://brainbright.herokuapp.com/brainbright-websocket');
//var socket = new SockJS('http://localhost:8080/brainbright-websocket');
var stompClient = Stomp.over(socket);

let submittedFieldID = " ";

function getWordx(client, level, isNew) {
    client.send("/app/wordex_get_letters", {}, JSON.stringify({ "level": level, "isNew": isNew }));
}

export default function ClientComponent() {
    //When switching menus, need to connect here instead
    if (!stompClient.connected) {
        //DOOOOOOOOOOO NOOOOT TOUCH THIS COMMENT !
        socket = new SockJS('https://brainbright.herokuapp.com/brainbright-websocket');
        //socket = new SockJS('http://localhost:8080/brainbright-websocket');
        stompClient = Stomp.over(socket);
    }
 
    //show on screen the level
    const [levelRender, setLevelRender] = useState(1);
    const [correctWordsRender, setCorrectWordsRender] = useState([]);
    const [letters, setLetters] = useState([]);
    let level = 1;
    let correctWords = 0;
    let correctWordArray = new Array();
    let letterBox = new Array(9);
    let word = "";
    useEffect(() => {
        if (!stompClient.connected) {
            stompClient.connect({}, function () {

                this.subscribe('/user/topic/wordex_get_letters', function (data) {
                    let letterArray = new Array();
                    JSON.parse(data.body).map(letter => {
                        letterArray.push(letter);
                    })
                    setLetters(letterArray);
                    
                });
                this.subscribe('/user/topic/wordex_validactionresponse', function (data) {
                    console.log(data.body, "test");
                });

                // if (level == 1) {
                //     getNapCharacter(this, 1, true);
                // }
                getWordx(this, 1, true);
            });
        }
    }, []);

    function getButtonContent(e) {
        //Push the new item into the array of buttons
        let buttonValue = e.target.innerHTML;
        word += buttonValue;
        console.log(word);
    }

    function sendWord(){
        console.log("sending to back ", word);
        stompClient.send("/app/wordex_validaction", {}, JSON.stringify({ "word": word}));  
    }

    return (
        <div className="container-fluid">

            <div className="row">
                <div className="col-md-3" id="player">
                    <Player name="PlayerName"></Player>
                    <Level level={levelRender}></Level>
                    <Menu></Menu>
                </div>

                <div className="col-md-6 text-center all_words mt-3">
                    
                    <div className="row">
                        {letters.map(letter => 
                            <div className="letterButton col-md-4 mb-2">
                                <button className="btn btn-primary " onClick={getButtonContent}>
                                    {letter}
                                </button>
                            </div>
                        )}
                    </div>
                    
                    <div className="result p-3 mt-5 rounded">
                    </div>

                    <div className="mt-2">
                        <button className="btn pl-5 pr-5 pt-2 pb-2 mt-3">Clear All</button>
                        <button className="btn pl-5 pr-5 pt-2 pb-2 mt-3">Clear Last</button>
                        <button className="btn pl-5 pr-5 pt-2 pb-2 mt-3" onClick={sendWord}>Submit</button>
                    </div>

                </div>
                

                <div className="col-md-3">
                    <div className="text-center">
                        <Sidebar level={levelRender} correctWords={correctWordsRender} elements={5*levelRender}></Sidebar>
                    </div>

                </div>

            </div>
        </div>
    )
}

