import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import splitWordLogo from '../../assets/images/splitwords.jpg';
import namesAnimalsPlantsLogo from '../../assets/images/namesanimalsplants.jpg';
import correctSpellingLogo from '../../assets/images/correctspelling.jpg';
import wordexLogo from '../../assets/images/wordex.jpg';
import wordSearchLogo from '../../assets/images/wordsearch.jpg';
import anagramLogo from '../../assets/images/anagram.jpg';



import SplitWord from "../splitWords/splitWords";
import NamesAnimalsPlants from "../namesAnimalsPlants/namesAnimalsPlants";
import CorrectSpelling from "../correctSpelling/correctSpelling";
import Wordex from "../wordex/wordex";
import WordSearch from "../wordSearch/wordSearch";
import Anagram from "../anagram/anagram";

import style from "./home.css";

export default function Home() {
    return (
        <div>
            <Router>
                <div className="container">
                    <div className="row justify-content-center">
                        <a href="/splitword">
                            <div className="card p-3 mr-2">
                                <div className="card-header">
                                    <img src={splitWordLogo} style={{width: "200px", height: "150px"}} />
                                </div>
                                <div className="card-body"><div className="title">Split Word</div></div>
                            </div>
                        </a>
                        <a href="/animals">
                            <div className="card p-3 mr-2">
                                <div className="card-header">
                                    <img src={namesAnimalsPlantsLogo} style={{width: "200px", height: "150px"}}/>
                                </div>
                                <div className="card-body"><div className="title">Names Animals Plants</div></div>
                            </div>
                        </a>
                        <a href="/correctspelling">
                            <div className="card p-3 mr-2">
                                <div className="card-header">
                                    <img src={correctSpellingLogo} style={{width: "200px", height: "150px"}}/>
                                </div>
                                <div className="card-body"><div className="title">Correct Spelling</div></div>
                            </div>
                        </a>

                        <a href="/anagram">
                            <div className="card p-3 mr-2">
                                <div className="card-header">
                                    <img src={anagramLogo} style={{width: "200px", height: "150px"}} />
                                </div>
                                <div className="card-body"><div className="title">Anagram</div></div>
                            </div>
                        </a>

                        <a href="/wordex">
                            <div className="card p-3 mr-2">
                                <div className="card-header">
                                    <img src={wordexLogo} style={{width: "200px", height: "150px"}}/>
                                </div>
                                <div className="card-body"><div className="title">Wordex</div></div>
                            </div>
                        </a>
                        <Link to="/wordsearch">
                            <div className="card p-3 mr-2">
                                <div className="card-header">
                                    <img src={wordSearchLogo} style={{width: "200px", height: "150px"}}/>
                                </div>
                                <div className="card-body"><div className="title">Word Search</div></div>
                            </div>
                        </Link>

                    </div>
                </div>
            </Router>
        </div>
    );
}
