import React, { Component, useState } from "react";
import "bootstrap/dist/css/bootstrap.css";
import style from "./sidebar.css";

class Sidebar extends Component {
    constructor(){
        super();
        this.state = {
            correctWords: new Array(0).fill(' ')
            
        }
    }

    updateFunction = () => {
        if(this.props.correctWords.length === 0){
            this.state.correctWords = new Array(this.props.elements).fill(' ')
        }else{
            for(let i = 0; i<this.props.correctWords.length; i++){
                this.state.correctWords[i] = this.props.correctWords[i];
            }
        }
    }


    render() {
        return (
            <div className="text-center">

                {
                    this.updateFunction()
                } 
                <div className="found">{"Found"}</div>
                <div className="border border-success"> {this.props.correctWords.length} out of {this.state.correctWords.length} </div>
                
                {
                    this.state.correctWords.map(correctWord =>
                        <div className="border border-dark resultrow" id={correctWord}>{correctWord}</div>
                    )
                }
            </div>
        )
    }
}

export default Sidebar;