import React, { Component } from "react";
import "bootstrap/dist/css/bootstrap.css";
import style from "./menu.css";

class Menu extends Component {

    render() {
        return (
            <div className="">
                <button className="btn mb-2 w-100">Instructions</button>
                <div className="row">
                    <div className="d-flex col-md-6">
                        <button className="btn mb-2  w-100">Pause</button>

                    </div>
                    <div className="col-md-6">
                        <button className="btn mb-2 w-100">Restart</button>
                    </div>
                    <div className="col-md-6">
                        <button className="btn mb-2 w-100">Return</button>

                    </div>
                    <div className="col-md-6">
                        <button className="btn mb-2 w-100">Exit</button>

                    </div>
                </div>
                <div className="row mt-4 text-center justify-content-center">
                    <div className="col-md-12 mb-2">
                    <span>Created by City College</span>
                    </div>
                    <button className="btn mr-2">[X]</button>
                    <button className="btn">[]</button>
                </div>
            </div>
        )
    }
}

export default Menu;

