import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.css";
import "../../assets/stylesheets/main_styles.css";
import * as Stomp from "stompjs";
import SockJS from "sockjs-client";
import splitWordStyle from "../splitWords/style.css";
import Player from "../player/player";
import Level from "../level/level";
import Menu from "../menu/menu";

//var socket = new SockJS("http://localhost:8080/brainbright-websocket");
var socket = new SockJS('https://brainbright.herokuapp.com/brainbright-websocket');
var stompClient = Stomp.over(socket);

function getAnagrams(client) {
  client.send("/app/getanagram", {}, null);
}

function getShuffledAnagrams(client) {
  client.send("/app/getshuffledlist", {}, null);
}

export default function ClientComponent() {
  const [anagramList, setAnagramList] = useState([]);
  const [anagramArray, setAnagramArray] = useState([]);
  const [shuffledArray, setShuffledArray] = useState([]);
  const [shuffledList, setShuffledList] = useState([]);
  const [shuffledWord, setShuffledWord] = useState("");
  const [anagramWord, setAnagramWord] = useState([]);
  const [correctWord, setCorrectWord] = useState("");
  const [resultArray, setResultArray] = useState("");
  const [buttonArray, setButtonArray] = useState([]);
  const [endResult, setEndResult] = useState("");

  useEffect(() => {
    stompClient.connect({}, function () {
      this.subscribe("/topic/anagramlist", function (data) {
        setAnagramArray(JSON.parse(data.body));
        let array = new Array(10);
        JSON.parse(data.body).map((item) => {
          array.push(item);
        });
        setAnagramList(array);
        setAnagramWord(array.pop());
      });

      getAnagrams(this);

      this.subscribe("/topic/shuffledwordlist", function (data) {
        setShuffledArray(JSON.parse(data.body));
        let shuffledArray = new Array(10);
        JSON.parse(data.body).map((item) => {
          shuffledArray.push(item);
        });
        setShuffledList(shuffledArray);
        setShuffledWord(shuffledArray.pop());
      });

      getShuffledAnagrams(this);
    });
  }, []);

  function getButtonContent(e) {
    let a = e.target.innerHTML;
    buttonArray.push(a);
    setResultArray([buttonArray]);
  }

  function clearContent() {
    setResultArray([]);
    setButtonArray([]);
    setEndResult("");
  }

  function checkValidity() {
    //let x = [];
    let count;
    let toArray = anagramWord.split("");

    for (let i = 0; i < toArray.length; i++) {
      if (buttonArray[i] == toArray[i]) {
        console.log(i);
        count = i;
      }
    }

    if (count == toArray.length - 1) {
      setEndResult("You are correct !");
    } else {
      setEndResult("You are wrong !");
    }
  }

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-3">
          <Player name="PlayerName"></Player>
          <Level level="1"></Level>
          <Menu></Menu>
        </div>
        <div className="col-md-6 text-center all_words">
          <h1>Scrambled word:</h1>
          <span>{shuffledWord}</span>

          <br></br>
          <br></br>

          <div className="row ">
            {resultArray.length === 0
              ? "Click a button..."
              : resultArray.map((item) => (
                  <button className="btn btn-primary mb-5 p-3 rounded border-0">
                    {item}
                  </button>
                ))}
          </div>
          <br></br>
          <br></br>

          <div className="row text-center">{endResult}</div>

          <br></br>
          <br></br>
          <div className="row ">
            {shuffledWord.split("").map((item) => (
              <div className="col-md-2 col-sm-3" key={item.word}>
                <button
                  className="btn btn-primary mb-5 p-3 rounded border-0"
                  id={item}
                  onClick={getButtonContent}
                >
                  {item}
                </button>
              </div>
            ))}
          </div>

          <div className="row">
            <button
              className="btn pl-5 pr-5 pt-2 pb-2 mt-3"
              onClick={clearContent}
            >
              Clear Content
            </button>
            <button
              className="btn pl-5 pr-5 pt-2 pb-2 mt-3"
              onClick={checkValidity}
            >
              Submit
            </button>
          </div>

          <br></br>
          <br></br>
          <br></br>
          <br></br>
        </div>
        <div className="col-md-3">
          <div className="border border-dark text-center">Test</div>
        </div>
      </div>
    </div>
  );
}
